button{
    display:block;
    background: none;
    border:none;
    padding:none;
    font-family:inherit;
    user-select:none;
    cursor:pointer;
    outline: none;
    -webkit-tap-highlight-color: rgba(0,0,0);
  }
  
  button:active {
    box-shadow: inset 0px 0px 80px 0px rgba(0,0,0.25);
  }

  .calculator {
    width: 320px;
    height: 520px;
    position:relative;
    /* width:100%;
    height: 100%; */
    background: black;
    display:flex;
    flex-direction:column;
    box-shadow: 0px 0px 20px 0px rgba(0,0,0,0.5);
    border-radius: 12px;
    overflow: hidden;
  }
  
  .calculator-display {
    color: white;
    background: #1c191c;
    line-height: 120px;
    font-size: 6em;
    flex: 1;
  }
  
  .calculator-display .auto-scaling-text {
    display: inline-block;
    padding: 0 30px;
    position: absolute;
    right: 0;
    transform-origin: right;
  }

  .calculator-display .auto-scaling-text.scale {
    transform: scaleX(0.6);
  }
  
  .calculator-keypad {
    height: 400px;
    display: flex;
  }
  
  .calculator .input-keys {
    width: 240px;
  }
  
  .calculator .function-keys {
    display: flex;
  }
  
  .calculator .number-keys {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap-reverse;
  }

  .calculator-key {
    width: 80px;
    height: 80px;
    border-top: 1px solid #777;
    border-right: 1px solid #666;
    text-align: center;
    line-height: 80px;
    cursor: pointer;
  }
  .calculator .function-keys .calculator-key {
    font-size: 2em;
  }
  .calculator .operator-keys #key-multiply {
    line-height: 100px;
  }
  .calculator .number-keys .calculator-key {
    font-size: 2.25em;
  }
  .calculator .number-keys #key-zero {
    width: 160px;
    text-align: left;
    padding-left: 32px;
  }
  .calculator .number-keys #key-decimal {
    /* padding-top: 1em; */
    font-size: 3.75em;
  }
  .calculator .operator-keys .calculator-key {
    color: white;
    border-right: 0;
    font-size: 3em;
  }
  
  :root {
    --function-color: #43464b;
    --clicked-function-color: rgb(98, 98, 98);
    --operator-color: #ffa600;
    --clicked-operator-color: rgb(215, 140, 0);
    --number-color: #a6aab0;
    --clicked-number-color:rgb(67, 70, 75, 1);
  }

  .calculator .function-keys {
    background-color: var(--function-color);
  }

  .calculator .function-keys .clicked {
    background-color: var(--clicked-function-color);
  }

  .calculator .operator-keys {
    background-color: var(--operator-color);
  }

  .calculator .operator-keys .clicked {
    background-color: var(--clicked-operator-color);
  }

  .calculator .number-keys {
    background-color: var(--number-color);
  }

  .calculator .number-keys .clicked {
    background-color: var(--clicked-number-color);
  }

  

  